import HelpIcon from "@mui/icons-material/Help";
import "./Register.scss";
import { useRef, useState } from "react";
import { Link, Route } from "react-router-dom";
import axios from "axios";

function Register() {
  const sexOptions = ["N", "Nam", "Ty chnh"];
  const [selectSex, setSelectSex] = useState(2);
  const firstNameRef = useRef(null);
  const lastNameRef = useRef(null);
  const [birthday, setDay] = useState("01");
  const [month, setMonth] = useState(1);
  const [yearBorn, setYear] = useState(new Date().getFullYear());
  const usernameRef = useRef();
  const passwordRef = useRef();

  const handleSex = (value) => {
    if (selectSex === value) {
      setSelectSex(false);
    } else setSelectSex(value);
  };
  const handleChangeDay = (event) => {
    setDay(event.target.value);
  };
  const handleChangeMonth = (event) => {
    setMonth(event.target.value);
  };
  const handleChangeYear = (event) => {
    setYear(event.target.value);
  };

  const clickButtonRegister = (event) => {
    event.preventDefault();
    const user = {
      userName: usernameRef.current.value,
      password: passwordRef.current.value,
      name: firstNameRef.current.value + " " + lastNameRef.current.value,
      date: `${yearBorn}-${String(Number(month)).padStart(2, "0")}-${String(
        birthday
      ).padStart(2, "0")}`,
      sex: selectSex,
    };
    console.log(user);
    axios
      .post("http://localhost:8080/user/register", user)
      .then((response) => {
        if (response.status === 200) {
          console.log("susscess");
          window.location.href = "http://localhost:3000/Login";
        } else {
          console.log(response.status);
        }
      })
      .catch(function (error) {
        console.error(error);
      });
  };

  return (
    <div className="register">
      <img src="https://static.xx.fbcdn.net/rsrc.php/y1/r/4lCu2zih0ca.svg" />
      <div className="formRegister">
        <div className="title">
          <div className="title1">To ti khon mi</div>
          <div className="title2">Nhanh chng v d dng</div>
        </div>
        <form>
          <div className="username">
            <input placeholder="H" ref={firstNameRef} />
            <input placeholder="Tn" ref={lastNameRef} />
          </div>
          <div className="bthday">
            <div className="birthdayTitle">
              Ngy sinh
              <a>
                <HelpIcon style={{ height: "0.9rem" }} />
              </a>
            </div>
            <div className="inputBirthday">
              <select onChange={handleChangeDay} value={birthday}>
                {Array.from({ length: 31 }, (_, i) => (
                  <option key={i + 1} value={i + 1}>
                    {i + 1}
                  </option>
                ))}
              </select>
              <select value={month} onChange={handleChangeMonth}>
                {Array.from({ length: 12 }, (_, i) => (
                  <option key={i + 1} value={i + 1}>
                    Thng {i + 1}
                  </option>
                ))}
              </select>
              <select value={yearBorn} onChange={handleChangeYear}>
                {Array.from({ length: 62 }, (_, i) => {
                  const yearBir = 1964 + 61 - i;
                  return (
                    <option key={yearBir} value={yearBir}>
                      {yearBir}
                    </option>
                  );
                })}
              </select>
            </div>
          </div>
          <div className="sex">
            <div className="sexTitle">
              Gii tnh
              <a>
                <HelpIcon style={{ height: "0.9rem" }} />
              </a>
            </div>
            <div className="sexContent">
              {sexOptions.map((sex, index) => {
                return (
                  <label key={sex}>
                    {sex}
                    <input
                      type="radio"
                      name="sex"
                      value={sex}
                      checked={selectSex === index}
                      onChange={() => handleSex(index)}
                    />
                  </label>
                );
              })}
            </div>
          </div>
          <div className="account">
            <input placeholder="S di ng hoc email" ref={usernameRef} />
            <input
              placeholder="Mt khu mi"
              type="password"
              ref={passwordRef}
            />
          </div>
          <div className="footerTitle">
            Nhng ngi dng dch v ca chng ti c th  ti thng tin lin
            h ca bn ln Facebook
          </div>
          <div className="footerTitle">
            Bng cch nhp vo ng k, bn ng  vi iu khon, Chnh sch
            quyn ring t v Chnh sch cookie ca chng ti. Bn c th nhn
            c thng bo ca chng ti qua SMS v hy nhn bt k lc no.
          </div>
          <div
            type="button"
            className="btRegister"
            onClick={clickButtonRegister}
          >
            <button>ng k</button>
          </div>
          <div className="rdLogin">
            <Link to="/">Bn  c ti khon ?</Link>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Register;
